body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eff4f9;
}

* {
  box-sizing: border-box;
}

.min-height-100vh {
  min-height: 100vh;
}
section, nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 140px 6vw;
  flex-direction: column;
}
nav {
  padding-top: 16px;
  padding-bottom: 16px;
}
.section-wrapper, .navbar-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1400px;
  flex-direction: column;
}
.left-align {
  align-items: flex-start;
}
.navbar-wrapper {
  flex-direction: row;
  justify-content: flex-end;
}
.button {
  padding: 10px 40px;
  background-color: #052D9C;
  color: white;
  border: 1px solid #052D9C;
  border-radius: 4px;
  font-size: 1rem;
  text-decoration: none;
}
.button:disabled {
  background-color: #efefee;
  border-color: #efefee;
  color: #052D9C;
  cursor: not-allowed;
}

.margin-bottom-10 {
  margin-bottom: 10px;
}
.margin-bottom-20 {
  margin-bottom: 20px;
}
.margin-top-40 {
  margin-top: 40px
}
.margin-top-20 {
  margin-top: 20px
}

.text-808080 {
  color: #808080;
}
.font-weight-700 {
  font-weight: 700;
}

.empty-result {
  text-align: center;
  color: #808080
}
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.flex-align-center {
  display: flex;
  align-items: center;
  gap: 10px;
}
.flex-vertical-start {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 20px;
}
.width-100 {
  width: 100%
}