.form {
  width: 100%;
  margin-top: 40px;
  max-width: 480px;
}
.auth-footer {
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 0.875rem;
  color: #808080;
  gap: 10px;
  margin-top: 20px;
}
.error {
  background-color: #f8e5e5;
  color: #D82E2B;
  text-align: center;
  padding: 8px;
  font-size: 0.875rem;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 20px
}
.show-password {
  position: absolute;
  right: 6px;
  font-size: 0.75rem;
  bottom: 8px;
  color: #808080;
  padding: 4px;
  cursor: pointer;
  background-color: white;
}
.password-strength-indicator {
  height: 4px;
  width: 5%;
  background-color: #D82E2B;
  border-radius: 64px;
  margin-top: 8px;
}
.password-indicator-text {
  font-size: 0.75rem;
  color: #D82E2B
}